import React, { useState } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '../Components/ui/card';
import { Button } from '../Components/ui/button';
import { Fan, Lightbulb, Droplets, PipetteIcon } from 'lucide-react';

const ControlButton = ({ active, onClick, children, variant = 'default' }) => {
    // Enhanced button styling with support for different variants
    const getButtonStyle = () => {
        if (variant === 'off' && active) {
            return 'bg-red-500 hover:bg-red-600 text-white';
        }
        if (active) {
            return 'bg-green-500 hover:bg-green-600 text-white';
        }
        return '';
    };

    return (
        <Button
            onClick={onClick}
            variant={active ? "default" : "outline"}
            className={`w-24 ${getButtonStyle()}`}
        >
            {children}
        </Button>
    );
};

const DeviceControl = ({ title, icon: Icon, count = 1, type }) => {
    const [states, setStates] = useState(Array(count).fill('off'));
    const [modes, setModes] = useState(Array(count).fill('manual'));

    const handleStateChange = (index, newState) => {
        const newStates = [...states];
        newStates[index] = newState;
        setStates(newStates);
    };

    const handleModeChange = (index) => {
        const newModes = [...modes];
        newModes[index] = modes[index] === 'manual' ? 'auto' : 'manual';
        setModes(newModes);
    };

    const getStatusStyle = (state) => {
        return state === 'on'
            ? 'text-green-500 font-medium'
            : 'text-red-500 font-medium';
    };

    return (
        <Card className="w-full">
            <CardHeader>
                <CardTitle className="flex items-center gap-2">
                    <Icon className="h-5 w-5" />
                    <span>{title}</span>
                </CardTitle>
            </CardHeader>
            <CardContent>
                <div className="grid grid-cols-1 gap-4">
                    {Array.from({ length: count }, (_, i) => (
                        <div key={i} className="flex flex-col gap-2">
                            <div className="flex items-center justify-between">
                                <span className="text-sm font-medium">{type} {i + 1}</span>
                                <span className={`text-sm ${getStatusStyle(states[i])}`}>
                                    Status: {states[i] === 'on' ? 'Running' : 'Stopped'}
                                </span>
                            </div>
                            <div className="flex gap-2">
                                <ControlButton
                                    active={states[i] === 'on'}
                                    onClick={() => handleStateChange(i, 'on')}
                                >
                                    On
                                </ControlButton>
                                <ControlButton
                                    active={states[i] === 'off'}
                                    onClick={() => handleStateChange(i, 'off')}
                                    variant="off"
                                >
                                    Off
                                </ControlButton>
                                <ControlButton
                                    active={modes[i] === 'auto'}
                                    onClick={() => handleModeChange(i)}
                                >
                                    {modes[i] === 'auto' ? 'Auto On' : 'Auto Off'}
                                </ControlButton>
                            </div>
                        </div>
                    ))}
                </div>
            </CardContent>
        </Card>
    );
};

const ControlPanel = () => {
    return (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-2 gap-6 p-6">
            <DeviceControl
                title="Fans"
                icon={Fan}
                count={4}
                type="Fan"
            />
            <DeviceControl
                title="Lights"
                icon={Lightbulb}
                count={4}
                type="Light"
            />
            <DeviceControl
                title="Humidifiers"
                icon={Droplets}
                count={2}
                type="Humidifier"
            />
            <DeviceControl
                title="Valves"
                icon={PipetteIcon}
                count={4}
                type="Valve"
            />
        </div>
    );
};

export default ControlPanel;